.training {
  border-radius: 20px;
  background: linear-gradient(213deg, #323e51 4.9%, #1d2532 55.16%);

  padding-bottom: 150px;

  &__title {
    position: relative;
    z-index: 10;
    text-align: center;
    font-size: 45px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%;

    color: var(--light-color);
    padding-top: 130px;
    padding-bottom: 65px;

    @include mobile {
      font-size: 23px;
      padding: 40px 72px 30px 72px;
    }
  }

  &__box {
    display: flex;
    justify-content: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    width: 650px;

    @include mobile {
      justify-content: center;
      align-items: center;
    }
  }

  &__item {
    width: 300px;
    height: 310px;

    border-radius: 20px;
    background: rgba(51, 62, 81, 1);
    backdrop-filter: blur(30.5px);

    margin-bottom: 70px;
    position: relative;
    z-index: 10;

    img {
      margin: 0 auto;
      width: 169px;
      height: 169px;

      @include mobile {
        width: 130px;
        height: 130px;
      }
    }

    &:nth-child(2n + 1) {
      margin-right: 50px;

      @include mobile {
        margin-right: 0px;
      }
    }

    &::before {
      content: "";
      display: inline-block;
      width: 260px;
      height: 268px;
      border-radius: 20px;
      background: rgba(51, 62, 81, 0.6);
      backdrop-filter: blur(27.5px);
      position: absolute;
      left: 50%;
      bottom: -30px;
      z-index: -1;
      transform: translateX(-50%);

      @include mobile {
        width: 226px;
        height: 235px;
        bottom: -20px;
      }
    }

    @include mobile {
      width: 260px;
      height: 273px;
      margin-bottom: 45px;
    }
  }

  &__descr {
    text-align: center;
    font-size: 19px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px; /* 131.579% */
    letter-spacing: -0.7px;

    color: var(--light-color);

    margin-top: 23px;

    @include mobile {
      font-size: 16px;
      padding: 0 23px 47px 23px;
    }
  }

  @include mobile {
    overflow: hidden;
  }
}

