/* stylelint-disable */

@mixin burger {
  --burger-width: 40px;
  --burger-height: 16px;
  --burger-line-height: 3px;

  position: relative;
  border: none;
  padding: 0;
  margin-right: 10px;
  width: var(--burger-width);
  height: var(--burger-height);
  background: #fbf7fe;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: #fbf7fe;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 15px;
  }

  &::after {
    top: 0;
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}

.button--close {
  display: none;
  @include mobile {
    position: absolute;
    right: 8%;
    top: 9%;
    display: block;
    width: 45px;
    height: 45px;
    cursor: pointer;

    &::before {
      content: "";
      display: block;
      width: 45px;
      height: 45px;
      background-image: url("../img/close.svg");
      background-position: 10% 5%;
      background-size: 50px 50px;
      background-repeat: no-repeat;
    }
  }
}

.burger-bcg {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background: rgba(48, 9, 97, 0.3);
  backdrop-filter: blur(7.5px);
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}

.burger-bcg-visible {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}
