.about {
  background-color: var(--light-color);
  min-height: 1000px;

  &__container {
    margin-top: 190px;
    margin-bottom: 190px;

    position: relative;

    @include mobile {
      border-radius: 20px;
      background: linear-gradient(213deg, #323e51 4.9%, #1d2532 55.16%);
      margin-bottom: 0;
    }
  }
}

.block {
  position: absolute;

  &-1 {
    width: 152px;
    height: 152px;
    background: linear-gradient(
      90deg,
      rgba(222, 168, 255, 1) 0%,
      rgba(157, 106, 217, 1) 44.51%,
      rgba(122, 43, 246, 1) 100%
    );
    border-radius: 100%;
    transform: rotate(25.732deg);

    top: -10%;
    right: 18%;
    z-index: 1;

    @include mobile {
      width: 87px;
      height: 87px;

      top: -3%;
      right: -10%;
    }
  }

  &-2 {
    width: 440px;
    height: 440px;
    background: linear-gradient(
      171deg,
      rgba(95, 56, 255, 0.25) 25.59%,
      rgba(255, 14, 222, 0.25) 100.44%
    );
    filter: blur(86px);
    border-radius: 100%;
    transform: rotate(25.732deg);

    top: 0%;
    right: 0%;
    z-index: 1;

    @include mobile {
      width: 137px;
      height: 137px;

      top: -5%;
      right: -15%;
    }
  }

  &-3 {
    width: 84px;
    height: 84px;

    background: linear-gradient(
      90deg,
      rgba(4, 93, 250, 1) 0%,
      rgba(15, 193, 215, 1) 83%,
      rgba(6, 246, 199, 1) 100%
    );
    transform: rotate(7.766deg);
    border-radius: 100%;

    top: 9%;
    left: 15%;
    z-index: 1;

    @include small-tablet {
      top: 2%;
    }

    @include mobile {
      width: 21px;
      height: 21px;

      top: 7%;
    }
  }

  &-4 {
    width: 440px;
    height: 440px;
    background: linear-gradient(
      171deg,
      rgba(95, 56, 255, 0.25) 25.59%,
      rgba(255, 14, 222, 0.25) 100.44%
    );
    filter: blur(86px);
    border-radius: 100%;
    transform: rotate(25.732deg);

    bottom: 0%;
    left: 0%;
    z-index: 1;

    @include mobile {
      display: none;
    }
  }

  &-5 {
    width: 132px;
    height: 132px;
    background: linear-gradient(
      90deg,
      rgba(222, 168, 255, 1) 0%,
      rgba(157, 106, 217, 1) 44%,
      rgba(122, 43, 246, 1) 100%
    );

    filter: blur(12px);
    transform: rotate(-25.732deg);
    border-radius: 100%;

    bottom: 15%;
    right: 12%;
    z-index: 1;

    @include mobile {
      width: 62px;
      height: 62px;

      bottom: 2%;
      right: 6%;
    }
  }
}
