.hero {
  background-color: var(--mirage);

  &__container {
    @include flex-all-center;

    @include mobile {
      display: block;
      position: relative;
    }
  }

  &__box {
    width: 860px;
    padding-top: 130px;
    margin-top: -25px;

    @include tablet {
      width: 750px;
    }

    @include small-tablet {
      padding-top: 200px;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__image {
    margin-top: 187px;
    margin-left: -310px;

    width: 863px;
    height: 708px;

    @include tablet {
      width: 700px;
      height: 650px;
      margin-left: -200px;
    }

    @include small-tablet {
      display: none;
    }

    @include mobile {
      display: block;
      overflow: hidden;
      width: 100%;
      height: 372px;
      margin-left: 0px;
      position: absolute;
      left: 0;
      top: 15%;

      img {
        width: 100%;
        height: 321px;
      }
    }
  }

  &__button {
    @include mobile {
      width: 100%;
    }
  }

  @include small-tablet {
    min-height: 700px;
  }

  @include mobile {
    min-height: 835px;
  }
}

.box {
  &__title {
    font-size: 72px;
    font-style: normal;
    font-weight: 400;
    line-height: 105%; /* 75.6px */
    letter-spacing: -2.16px;

    color: var(--light-color);
    margin-bottom: 20px;

    span {
      font-weight: 700;
    }

    @include tablet {
      font-size: 61px;
    }

    @include mobile {
      font-size: 25px;
      letter-spacing: -0.75px;
      text-align: center;
      margin-bottom: 348px;
    }
  }

  &__descr {
    font-size: 23px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;

    width: 500px;

    color: var(--alto);
    margin-bottom: 67px;

    @include mobile {
      font-size: 18px;
      text-align: center;
      width: 100%;
      margin-bottom: 40px;
    }
  }

  &__btn {
    text-align: center;
    font-family: "GTWalsheimPro-Medium";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%;

    padding: 20px 30px;
    border-radius: 20px;
    background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
    box-shadow: 0px 4px 9px 7px rgba(29, 216, 68, 0.25);

    color: var(--mine-shaft);
    transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

    &:disabled {
      color: rgba(36, 36, 36, 0.5);
      background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
      box-shadow: 0px 0px 0px 0px rgba(29, 216, 68, 0.25);
    }

    &:hover {
      color: var(--mirage);
      background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
      box-shadow: 0px 4px 19px 9px rgba(29, 216, 68, 0.25);
      transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
    }

    &:active {
      color: var(--mirage);
      background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
      box-shadow: 0px 4px 19px 9px rgba(29, 216, 68, 0.25);
      transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
    }
  }
}
