.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;

  padding-top: 32px;

  &__container {
    position: relative;
    @include flex-all-sb;
  }

  &__btn {
    padding: 16px 22px;
    border: 1px solid var(--light-color);
    border-radius: 15px;

    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    color: var(--light-color);
    transition: color 0.2s ease-in-out, border 0.2s ease-in-out;

    &:hover {
      color: var(--starship);
      border: 1px solid var(--starship);
      transition: color 0.2s ease-in-out, border 0.2s ease-in-out;
    }

    &:disabled {
      color: var(--light-color);
      border: 1px solid var(--light-color);
      transition: color 0.2s ease-in-out, border 0.2s ease-in-out;
    }

    &:active {
      color: var(--starship);
      border: 1px solid var(--starship);
      background: rgba(224, 255, 172, 0.05);
      backdrop-filter: blur(7.5px);
      transition: color 0.2s ease-in-out, border 0.2s ease-in-out;
    }

    @include big-desktop {
      margin-left: 50px;
    }

    @include small-tablet {
      margin-left: 0;
    }
  }
}

.block-6 {
  width: 440px;
  height: 440px;
  background: linear-gradient(
    171deg,
    rgba(95, 56, 255, 0.25) 25.59%,
    rgba(255, 14, 222, 0.25) 100.44%
  );
  filter: blur(86px);
  border-radius: 100%;
  transform: rotate(25.732deg);

  top: -100%;
  left: -20%;
  z-index: -1;

  @include mobile {
    display: none;
  }
}
