.graph-modal.is-open {
  opacity: 1;
  background: rgba(48, 9, 97, 0.3);
  backdrop-filter: blur(7.5px);
  display: flex;
  justify-content: flex-start;
}

.graph-modal__container {
  position: relative;
  z-index: 10;
  width: 670px;
  border-radius: 30px;
  padding: 56px 145px 90px 70px;
  background: radial-gradient(
    207.54% 112.44% at 15.58% -40.39%,
    #323e51 0%,
    #1d2532 100%
  );
  overflow: hidden;

  &::before {
    content: "";
    width: 424px;
    height: 386px;
    transform: rotate(11.969deg);

    border-radius: 424.927px;
    background: linear-gradient(
      171deg,
      rgba(195, 243, 58, 0.25) 25.59%,
      rgba(41, 209, 89, 0.25) 100.44%
    );
    filter: blur(86px);

    position: absolute;
    right: -15%;
    top: -20%;
    z-index: -1;

    @include mobile {
      width: 287px;
      height: 261px;
      top: 40%;
    }
  }

  @include mobile {
    width: 100%;
    padding: 85px 30px 40px 15px;
    height: 400px;
  }
}

.modal {
  width: 460px;

  &__title {
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: 105%; /* 47.25px */
    letter-spacing: -1.35px;

    color: var(--light-color);
    margin-bottom: 25px;

    @include mobile {
      font-size: 23px;
      line-height: 130%;
      margin-bottom: 15px;
    }
  }

  &__descr {
    font-size: 21px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;

    color: var(--light-color);
    margin-bottom: 35px;

    @include mobile {
      font-size: 18px;
    }
  }

  &__input {
    width: 100%;
    padding: 15px 268px 15px 20px;

    border-radius: 10px;
    background-color: var(--light-color);
    margin-bottom: 18px;

    @include mobile {
      padding: 15px 128px 15px 15px;
    }
  }

  &__btn {
    width: 100%;
  }

  &__close {
    right: 6%;
    top: 6%;
    width: 45px;
    height: 45px;

    cursor: pointer;
  }

  @include mobile {
    width: 310px;
  }
}
