html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  overflow-anchor: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.button {
  text-align: center;
  font-family: "GTWalsheimPro-Medium";
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%;

  padding: 20px 30px;
  border-radius: 20px;
  background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
  box-shadow: 0px 4px 9px 7px rgba(29, 216, 68, 0.25);

  color: var(--mine-shaft);
  transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;

  &:disabled {
    color: rgba(36, 36, 36, 0.5);
    background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
    box-shadow: 0px 0px 0px 0px rgba(29, 216, 68, 0.25);
  }

  &:hover {
    color: var(--mirage);
    background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
    box-shadow: 0px 4px 19px 9px rgba(29, 216, 68, 0.25);
    transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  }

  &:active {
    color: var(--mirage);
    background: linear-gradient(93deg, #1dd844 1.44%, #cbf62e 126.29%);
    box-shadow: 0px 4px 19px 9px rgba(29, 216, 68, 0.25);
    transition: color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  }

  @include mobile {
    padding: 15px 30px;
    font-size: 16px;
  }
}
