.nav {
  &__list {
    @include flex-v-center;

    @include mobile {
      display: block;
      width: 214px;
      height: 76px;
      margin: 115px 70px 90px 20px;
    }
  }

  &__item:not(:last-child) {
    margin-right: 150px;

    @include big-desktop {
      margin-right: 100px;
    }

    @include tablet {
      margin-right: 50px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__link {
    font-size: 23px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    color: var(--alto);

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 23px;

      color: var(--alto);
    }
  }

  @include mobile {
    position: fixed;
    right: 0;
    top: 0;
    z-index: 500;
    overflow-y: hidden;
    width: 304px;
    height: 392px;
    border-radius: 0px 0px 0px 30px;
    background-color: #293344;
    background-image: url("../img/logo.svg");
    background-position: 10% 5%;
    background-size: 89px 85px;
    background-repeat: no-repeat;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}

.nav__item:nth-child(2) {
  @include mobile {
    margin-bottom: 90px;
  }
}

.burger {
  display: none;
  @include mobile {
    display: block;
    @include burger;
  }
}
